import notify from "@/public/notify";
import { DownloadingLoopIcon, LoadingSpinnerIcon } from "@wptb/ui/icons";
import cn from "classnames";
import { useEffect, useState } from "react";

let INFO_CACHE: {
  version: string;
  latest: string;
  versions: Record<string, string>;
  changelog: Record<string, string>;
} | null = null;

const parseChangelog = (changelog: string) => {
  const changeLog: Record<string, string> = {};
  const div = document.createElement("div");
  div.innerHTML = changelog;
  const allH4 = div.querySelectorAll("h4");
  allH4.forEach((h4) => {
    const version = h4.textContent?.trim();
    if (version) {
      changeLog[version] = h4.nextElementSibling?.outerHTML || "";
    }
  });
  return changeLog;
};

const VersionSettings = () => {
  const [isLoading, setIsLoading] = useState(INFO_CACHE === null);
  const [isSaving, setIsSaving] = useState(false);
  const [info, setInfo] = useState(INFO_CACHE);
  const [v, setV] = useState(INFO_CACHE?.version || "");
  useEffect(() => {
    if (INFO_CACHE !== null) {
      setIsLoading(false);
      setInfo(INFO_CACHE);
      setV(INFO_CACHE.version);
      return;
    }
    fetch("https://api.wordpress.org/plugins/info/1.0/wp-table-builder.json")
      .then((response) => response.json())
      .then((data) => {
        if (INFO_CACHE) {
          return;
        }
        const versions: Record<string, string> = {};
        const versionsKeys = Object.keys(data.versions).reverse().slice(1, 6);
        versionsKeys.forEach((key) => {
          versions[key] = data.versions[key];
        });
        INFO_CACHE = {
          version: data.version,
          versions,
          changelog: parseChangelog(data.sections.changelog),
          latest: Object.keys(data.versions).at(-2) as string,
        };
        setInfo(INFO_CACHE);
        setV(INFO_CACHE.version);
      })
      .finally(() => {
        setIsLoading(false);
      });
  }, []);

  const install = async () => {
    if (isSaving) return;
    setIsSaving(true);
    const fd = new FormData();
    fd.append("action", "version_control_manager");
    fd.append("nonce", WPTB_CFG.SETTINGS.nonce.version);
    fd.append("version", v);

    try {
      const res = await fetch(WPTB_CFG.ADMIN_URL, {
        method: "POST",
        body: fd,
      });
      const data = await res.json();
      if (!res.ok) {
        notify(data.message || "Something went wrong", "error");
        return;
      }
      if (data.error) {
        notify(data.error, "error");
        return;
      }
      notify(data.message || "Settings saved successfully", "success");
      window.location.reload();
    } catch (_) {
      notify("Something went wrong", "error");
    }
    setIsSaving(false);
  };

  return (
    <div className="wptb-v2-settings-area wptb-v2-version-settings">
      {isLoading || !info ? (
        <div className="wptb-v2-loading">
          <DownloadingLoopIcon />
        </div>
      ) : (
        <div className="wptb-v2-version-settings-content">
          <div className="wptb-v2-version-settings-control">
            <p className="wptb-v2-version-settings-encourage">
              If you are having issues with the current version of the plugin,
              you can rollback to a previous version.
            </p>
            <div className="wptb-v2-version-settings-info">
              <div>Current Version</div>
              <div>
                <div
                  className={cn({
                    "wptb-v2-version-settings-info-success":
                      info.latest === WPTB_CFG.SETTINGS.version,
                    "wptb-v2-version-settings-info-warning":
                      info.latest !== WPTB_CFG.SETTINGS.version,
                  })}
                ></div>
                <span>{WPTB_CFG.SETTINGS.version}</span>
              </div>
              <div>Latest Version</div>
              <div>
                <div
                  className={cn({
                    "wptb-v2-version-settings-info-success":
                      info.latest === WPTB_CFG.SETTINGS.version,
                    "wptb-v2-version-settings-info-warning":
                      info.latest !== WPTB_CFG.SETTINGS.version,
                  })}
                ></div>
                <span>{info.latest}</span>
              </div>
              <div>Install</div>
              <div>
                <select value={v} onChange={(e) => setV(e.target.value)}>
                  {Object.keys(info.versions).map((version) => (
                    <option key={version} value={version}>
                      {version}
                    </option>
                  ))}
                </select>
              </div>
            </div>
            <div className="wptb-v2-version-settings-warning">
              <h5>warning:</h5>
              <p>
                Previous versions may be unstable and unsecure, continue with
                your own risk and take a backup.
              </p>
            </div>
          </div>
          <div className="wptb-v2-version-settings-log">
            <h4>{v}</h4>
            <div
              dangerouslySetInnerHTML={{ __html: info.changelog[v] || "" }}
            ></div>
          </div>
        </div>
      )}
      <div className="wptb-v2-setting-btns">
        <button
          type="button"
          className="wptb-btn-save"
          disabled={isSaving}
          onClick={install}
        >
          {isSaving && LoadingSpinnerIcon} Install {v}
        </button>
      </div>
    </div>
  );
};

export default VersionSettings;
