import Nav from "../Nav";
import {
  Sorting,
  TableEntry,
  TablesPaginationInfo,
  useSorting,
  useTablePage,
} from "@wptb/utils/react";
import { Pagination, PopUp, TableList } from "@wptb/ui/components";
import { useEffect, useRef, useState } from "react";
import { DownloadingLoopIcon } from "@wptb/ui/icons/loaders";
import notify from "@/public/notify";

interface AllTableProps {
  onSelect: (table: TableEntry) => void;
  onAllSelect: () => void;
  apiData: TablesPaginationInfo;
  searchTxt: string;
  setSearch: (txt: string) => void;
  items: TableEntry[];
  sorting: [Sorting, (by: string) => void];
}

const AllTables = ({
  onSelect,
  onAllSelect,
  apiData,
  searchTxt,
  setSearch,
  items,
  sorting,
}: AllTableProps) => {
  return (
    <>
      <div className="wptb-v2-export-page-header">
        <h2>All Tables</h2>
        <div className="wptb-v2-export-page-search">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="1em"
            height="1em"
            viewBox="0 0 48 48"
          >
            <g
              fill="none"
              stroke="currentColor"
              strokeLinejoin="round"
              strokeWidth="4"
            >
              <path d="M21 38c9.389 0 17-7.611 17-17S30.389 4 21 4S4 11.611 4 21s7.611 17 17 17Z" />
              <path
                strokeLinecap="round"
                d="M26.657 14.343A7.98 7.98 0 0 0 21 12a7.98 7.98 0 0 0-5.657 2.343m17.879 18.879l8.485 8.485"
              />
            </g>
          </svg>
          <input
            type="search"
            placeholder="Search..."
            value={searchTxt}
            onChange={(e) => setSearch(e.target.value)}
          />
        </div>
      </div>
      <p className="wptb-pagination-info">
        Showing <b>{apiData.page_start}</b> to <b>{apiData.page_end}</b> of{" "}
        <b>{apiData.total_count}</b> entries
      </p>
      <TableList
        onSelect={onSelect}
        onAllCheck={onAllSelect}
        sorting={sorting}
        isLoading={apiData.loading}
        items={items}
        columns={["title", "modified", "id"]}
        checkbox
      />
    </>
  );
};

interface SelectedTableProps {
  onUnSelect: (table: TableEntry) => void;
  items: TableEntry[];
  onAllUnSelect?: () => void;
}

const SelectedTables = ({
  onUnSelect,
  onAllUnSelect,
  items,
}: SelectedTableProps) => {
  const sorting = useSorting<keyof TableEntry>("modified", "desc");
  const [searchTxt, setSearch] = useState("");
  const [tables, setTables] = useState<TableEntry[]>(items);

  useEffect(() => {
    setTables(items);
  }, [items]);

  useEffect(() => {
    if (searchTxt === "") {
      return setTables(items);
    }
    const filtered = items.filter((t) =>
      t.title.toLowerCase().includes(searchTxt.toLowerCase())
    );
    setTables(filtered);
  }, [searchTxt, items]);

  const sort = sorting[0];
  useEffect(
    () =>
      setTables((tables) => {
        if (sort.order === "asc") {
          tables.sort((a: TableEntry, b: TableEntry) => {
            return a[sort.by].toString().localeCompare(b[sort.by].toString());
          });
        } else {
          tables.sort((a: TableEntry, b: TableEntry) => {
            return b[sort.by].toString().localeCompare(a[sort.by].toString());
          });
        }
        return [...tables];
      }),
    [sort]
  );

  return (
    <>
      <div className="wptb-v2-export-page-header">
        <h2>To Export</h2>
        <div className="wptb-v2-export-page-search">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="1em"
            height="1em"
            viewBox="0 0 48 48"
          >
            <g
              fill="none"
              stroke="currentColor"
              strokeLinejoin="round"
              strokeWidth="4"
            >
              <path d="M21 38c9.389 0 17-7.611 17-17S30.389 4 21 4S4 11.611 4 21s7.611 17 17 17Z" />
              <path
                strokeLinecap="round"
                d="M26.657 14.343A7.98 7.98 0 0 0 21 12a7.98 7.98 0 0 0-5.657 2.343m17.879 18.879l8.485 8.485"
              />
            </g>
          </svg>
          <input
            type="search"
            placeholder="Search..."
            value={searchTxt}
            onChange={(e) => setSearch(e.target.value)}
          />
        </div>
      </div>

      <TableList
        onSelect={onUnSelect}
        onAllCheck={onAllUnSelect}
        sorting={sorting}
        items={tables}
        columns={["title", "modified", "id"]}
        checkbox
        checked
        noItemsMessage="No tables selected"
      />
    </>
  );
};

const savingEl = document.createElement("a");

const exportTables = async (ids: string, format: string) => {
  const fd = new FormData();
  fd.append("ids", ids);
  try {
    const formData = new FormData();

    formData.append("nonce", WPTB_CFG.EXPORT_KEY);
    formData.append("action", "wptb_table_export_main_export");
    formData.append("ids", JSON.stringify(ids.split(",")));
    formData.append("export_type", format.toLocaleUpperCase());

    const r = await fetch(WPTB_CFG.ADMIN_URL, {
      method: "POST",
      body: formData,
    });
    let resp: any;
    let filename: string = "";

    const contentType = r.headers.get("content-type");

    if (contentType === "application/octet-stream") {
      const pFileName = r.headers.get("content-disposition");
      const regex = new RegExp(/filename="(.+\..+)"/, "g");
      const results = regex.exec(pFileName || "");
      if (results === null) {
        throw new Error("Invalid file name header!");
      }
      filename = results[1];
      resp = await r.blob();
    } else {
      resp = await r.json();
    }

    const objectData = window.URL.createObjectURL(resp);
    savingEl.href = objectData;
    savingEl.download = filename;
    savingEl.click();

    window.URL.revokeObjectURL(objectData);
    notify('File downloaded as "' + filename + '"', "success");
  } catch (e: any) {
    notify(e.message || "Something went wrong", "error");
  }
};

const FORMAT_DETAILS = {
  xml: (
    <>
      <b>XML:</b> an exact copy of your tables will be exported, ideal for
      backing up and sharing your tables with your other WordPress sites that
      uses WP Table Builder.
    </>
  ),
  csv: (
    <>
      <b>CSV:</b> only text content of your tables will be exported, ideal for
      usage within other apps/plugins.
    </>
  ),
} as const;

interface PageProps {
  setPage: (page: string) => void;
}

const ExportTablePage = ({ setPage }: PageProps) => {
  const abortCtrl = useRef<AbortController>();
  const [page, setPageNum] = useState(1);
  const sorting = useSorting<string>("modified", "desc");
  const [searchTxt, setSearch] = useState("");
  const [tables, setTables] = useState<any>([]);
  const [selectedIds, setSelectedIds] = useState<string>("");

  const [format, setFormat] = useState("xml");
  const [exporting, setExporting] = useState(false);

  const [apiData, _, refetch] = useTablePage({
    page,
    search: searchTxt,
    sort: sorting[0],
    abortCtrl: abortCtrl.current,
    skipTables: selectedIds,
  });

  useEffect(() => {
    setTables(apiData.posts);
  }, [apiData.posts]);
  const [selected, setSelected] = useState<TableEntry[]>([]);

  const onSelect = (table: TableEntry) => {
    const ids = selectedIds + "," + table.id;
    setSelectedIds(ids.replace(/^,/, ""));
    setSelected([...selected, table]);
    setTables(tables.filter((t: TableEntry) => t.id !== table.id));
    if (tables.length <= 1) {
      refetch();
    }
  };

  const onUnSelect = (table: TableEntry) => {
    setSelected(selected.filter((t: TableEntry) => t.id !== table.id));
    setTables([...tables, table]);
    let ids = selectedIds.replace(table.id.toString(), "");
    ids = ids.replace(/,,/, ",");
    ids = ids.replace(/(^,)|(,$)/, "");
    setSelectedIds(ids);
  };

  const onAllSelect = () => {
    if (!tables.length) {
      return;
    }
    const nSelected = [...selected];
    let ids = selectedIds;

    tables.forEach((t: TableEntry) => {
      nSelected.push(t);
      ids = ids + "," + t.id;
    });

    ids = ids.replace(/^,/, "");

    setSelected(nSelected);
    setSelectedIds(ids);
    refetch();
  };
  const onAllUnSelect = () => {
    if (!selected.length) {
      return;
    }
    setSelected([]);
    setSelectedIds("");
    setTables([...tables, ...selected]);
  };

  const doExport = () => {
    if (exporting) {
      return;
    }
    if (!selectedIds) {
      notify("No tables selected", "error");
      return;
    }
    setExporting(true);
    exportTables(selectedIds, format).then(() => {
      setExporting(false);
      onAllUnSelect();
    });
  };

  return (
    <div className="wptb-v2-page">
      <Nav setPage={setPage} page="export" />
      <div className="wptb-v2-page-content">
        <div className="wptb-v2-page-content-top">
          <h3>Export Table</h3>
        </div>
        <div className="wptb-v2-export-page">
          <div className="wptb-v2-export-page-unselected">
            <AllTables
              onSelect={onSelect}
              onAllSelect={onAllSelect}
              apiData={apiData}
              setSearch={setSearch}
              searchTxt={searchTxt}
              items={tables}
              sorting={sorting}
            />
            <Pagination apiData={apiData} setPage={setPageNum} />
          </div>
          <div className="wptb-v2-export-page-exchange">
            <button type="button" onClick={onAllSelect}>
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="1em"
                height="1em"
                viewBox="0 0 24 24"
              >
                <g fill="none">
                  <path d="M24 0v24H0V0zM12.593 23.258l-.011.002l-.071.035l-.02.004l-.014-.004l-.071-.035q-.016-.005-.024.005l-.004.01l-.017.428l.005.02l.01.013l.104.074l.015.004l.012-.004l.104-.074l.012-.016l.004-.017l-.017-.427q-.004-.016-.017-.018m.265-.113l-.013.002l-.185.093l-.01.01l-.003.011l.018.43l.005.012l.008.007l.201.093q.019.005.029-.008l.004-.014l-.034-.614q-.005-.019-.02-.022m-.715.002a.02.02 0 0 0-.027.006l-.006.014l-.034.614q.001.018.017.024l.015-.002l.201-.093l.01-.008l.004-.011l.017-.43l-.003-.012l-.01-.01z" />
                  <path
                    fill="currentColor"
                    d="m15.06 5.283l5.657 5.657a1.5 1.5 0 0 1 0 2.12l-5.656 5.658a1.5 1.5 0 0 1-2.122-2.122l3.096-3.096H4.5a1.5 1.5 0 0 1 0-3h11.535L12.94 7.404a1.5 1.5 0 0 1 2.122-2.121Z"
                  />
                </g>
              </svg>
            </button>
            <button type="button" onClick={onAllUnSelect}>
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="1em"
                height="1em"
                viewBox="0 0 24 24"
              >
                <g fill="none">
                  <path d="M24 0v24H0V0zM12.593 23.258l-.011.002l-.071.035l-.02.004l-.014-.004l-.071-.035q-.016-.005-.024.005l-.004.01l-.017.428l.005.02l.01.013l.104.074l.015.004l.012-.004l.104-.074l.012-.016l.004-.017l-.017-.427q-.004-.016-.017-.018m.265-.113l-.013.002l-.185.093l-.01.01l-.003.011l.018.43l.005.012l.008.007l.201.093q.019.005.029-.008l.004-.014l-.034-.614q-.005-.019-.02-.022m-.715.002a.02.02 0 0 0-.027.006l-.006.014l-.034.614q.001.018.017.024l.015-.002l.201-.093l.01-.008l.004-.011l.017-.43l-.003-.012l-.01-.01z" />
                  <path
                    fill="currentColor"
                    d="M3.283 10.94a1.5 1.5 0 0 0 0 2.12l5.656 5.658a1.5 1.5 0 1 0 2.122-2.122L7.965 13.5H19.5a1.5 1.5 0 0 0 0-3H7.965l3.096-3.096a1.5 1.5 0 1 0-2.122-2.121z"
                  />
                </g>
              </svg>
            </button>
          </div>
          <div className="wptb-v2-export-page-selected">
            <SelectedTables
              onUnSelect={onUnSelect}
              onAllUnSelect={onAllUnSelect}
              items={selected}
            />
            <div className="wptb-v2-export-page-form">
              <p>Format:</p>
              <select
                value={format}
                onChange={(e) => setFormat(e.target.value)}
              >
                <option value="xml">XML</option>
                <option value="csv">CSV</option>
              </select>
              <PopUp
                content={FORMAT_DETAILS[format as keyof typeof FORMAT_DETAILS]}
                className="wptb-v2-export-page-form-info"
              >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="1em"
                  height="1em"
                  viewBox="0 0 24 24"
                >
                  <path
                    fill="currentColor"
                    d="M12 22C6.477 22 2 17.523 2 12S6.477 2 12 2s10 4.477 10 10s-4.477 10-10 10m-1-7v2h2v-2zm2-1.645A3.502 3.502 0 0 0 12 6.5a3.5 3.5 0 0 0-3.433 2.813l1.962.393A1.5 1.5 0 1 1 12 11.5a1 1 0 0 0-1 1V14h2z"
                  />
                </svg>
              </PopUp>
              <button type="button" disabled={exporting} onClick={doExport}>
                {exporting ? (
                  <DownloadingLoopIcon />
                ) : (
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    width="1em"
                    height="1em"
                    viewBox="0 0 256 256"
                  >
                    <path
                      fill="currentColor"
                      d="M74.34 85.66a8 8 0 0 1 11.32-11.32L120 108.69V24a8 8 0 0 1 16 0v84.69l34.34-34.35a8 8 0 0 1 11.32 11.32l-48 48a8 8 0 0 1-11.32 0ZM240 136v64a16 16 0 0 1-16 16H32a16 16 0 0 1-16-16v-64a16 16 0 0 1 16-16h52.4a4 4 0 0 1 2.83 1.17L111 145a24 24 0 0 0 34 0l23.8-23.8a4 4 0 0 1 2.8-1.2H224a16 16 0 0 1 16 16m-40 32a12 12 0 1 0-12 12a12 12 0 0 0 12-12"
                    />
                  </svg>
                )}
                Export
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ExportTablePage;
