import { __ } from "@wptb/utils/i18n";
import Nav from "../Nav";
import NewTable from "./NewTable";
import Patterns from "./Patterns";
import { useEffect, useRef, useState } from "react";
import { SearchControl } from "@wptb/ui/components";

interface PageProps {
  setPage: (page: string, pageProps?: any) => void;
  search?: string;
}
const CreateTablePage = ({ setPage, search }: PageProps) => {
  const [searchTxt, setSearch] = useState(search || "");
  const tOutRef = useRef<NodeJS.Timeout>();

  useEffect(() => {
    const newSearch = searchTxt?.trim();
    if (!search) {
      if (!newSearch || newSearch.length < 2) {
        return;
      }
    } else if (newSearch && newSearch.length > 0 && newSearch.length < 2) {
      return;
    }
    if (tOutRef.current) {
      clearTimeout(tOutRef.current);
    }
    tOutRef.current = setTimeout(() => {
      setPage("create", {
        search: searchTxt,
      });
      tOutRef.current = undefined;
    }, 500);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [searchTxt]);
  return (
    <div className="wptb-v2-page wptb-v2-create-page">
      <Nav setPage={setPage} page="create" />
      <div className="wptb-v2-page-content">
        <div className="wptb-v2-page-content-top">
          <h2>{__("New Table")}</h2>
          <SearchControl
            value={searchTxt}
            onChange={setSearch}
            placeholder="Search templates..."
          />
        </div>
        <div className="wptb-v2-page-content-main">
          <NewTable setPage={setPage} />
          <div className="wptb-v2-divider">or</div>
            <h2>Choose from templates</h2>
          <Patterns setPage={setPage} search={search} />
        </div>
      </div>
    </div>
  );
};

export default CreateTablePage;
