import { useEffect, useState } from "react";
import store from "@/store";
import notify from "@/public/notify";
import parseTable from "@/legacy/parser";
import { changeId, setTable } from "@/store/table";
import { isNumeric } from "@wptb/utils/check";
import BuilderApp from "@/builder";
import { useDispatch } from "react-redux";
import { setSettings } from "@/store/editor";

interface PageProps {
  table: string;
  setPage: (page: string, pageProps?: any) => void;
}

const BuilderPage = ({ table, setPage }: PageProps) => {
  const dispatch = useDispatch();
  const [isLoaded, setIsLoaded] = useState(false);
  const [tblId, setTblId] = useState(table);
  useEffect(() => {
    setTblId(table);
    dispatch(
      setSettings({
        isSaving: false,
        activeBlock: undefined,
        timeTravel: {
          lastSaved: /[^0-9]/.test(table) ? undefined : 0,
          cursor: 0,
          total: 0,
        },
      })
    );
    if (!isNumeric(table)) {
      if (!store.getState().table[table]) {
        history.replaceState(null, "", "?page=wptb");
        setPage("tables");
        return;
      }
      setIsLoaded(true);
      return;
    }
    setIsLoaded(false);
    const abortCtrl = new AbortController();
    const url = WPTB_CFG.API_BASE + "/v1/table?id=" + table;
    fetch(url, {
      signal: abortCtrl.signal,
    })
      .then((response) => response.json())
      .then((data) => {
        if (!data.table) {
          notify("Table not found", "error", 5000);
          history.replaceState(null, "", "?page=wptb");
          setPage("tables");
          return;
        }
        const tableState = parseTable(data.table)!;
        tableState.title = data.name;
        tableState.id = table;
        store.dispatch(
          setTable({
            id: table,
            table: tableState,
          })
        );
        setIsLoaded(true);
      })
      .catch((_) => null);

    return () => abortCtrl.abort();
  }, [table, setPage, dispatch]);

  const onSave = ([action, id]: [string, number]) => {
    if (action === "saved") {
      setTblId(id.toString());
      dispatch(
        changeId({
          oldId: table,
          newId: id.toString(),
        })
      );
      history.replaceState(null, "", "?page=wptb-builder&table=" + id);
    }
  };

  const onClose = () => {
    if (history.length > 2) {
      history.back();
      return;
    }
    setPage("tables");
  };

  return (
    <BuilderApp
      id={tblId}
      isLoaded={isLoaded}
      onSave={onSave}
      onClose={onClose}
    />
  );
};
export default BuilderPage;
