import { useEffect, useRef, useState } from "react";
import Nav from "../Nav";
import cn from "classnames";
import { TimesIcon, TrashIcon } from "@wptb/ui/icons/actions";
import { processFiles, sendImportRequest, ZipEntry } from "./utils";
import { EllipsisLoadingIcon, UploadingLoopIcon } from "@wptb/ui/icons/loaders";
import notify from "@/public/notify";
import { CheckSign, ErrorSign } from "@wptb/ui/icons/sign";
import { PopUp } from "@wptb/ui/components";

const getStatusIcon = (status?: string | number) => {
  if (!status) {
    return "";
  }
  if (status === 1) {
    return EllipsisLoadingIcon;
  }
  if (status === 2) {
    return CheckSign;
  }
  return <PopUp content={status}>{ErrorSign}</PopUp>;
};

interface PageProps {
  setPage: (page: string) => void;
}

const ImportTablePage = ({ setPage }: PageProps) => {
  const [dragOver, setDragOver] = useState(false);
  const [keepTitles, setKeepTitles] = useState(true);
  const [zips, setZips] = useState<ZipEntry[]>([]);
  const [rawFiles, setRawFiles] = useState<Array<File & { status?: number }>>(
    []
  );
  const [loadings, setLoadings] = useState<Record<string, number>>({});
  const [isLoading, setLoading] = useState(false);
  const setLoadingsRef = useRef<typeof setLoadings>(setLoadings);

  useEffect(() => {
    setLoadingsRef.current = setLoadings;
  }, [setLoadings]);

  const onDrop = async (e: any) => {
    e.preventDefault();
    e.stopPropagation();
    setDragOver(false);
    if (isLoading) {
      notify("Please wait for the previous import to finish.", "error");
      return;
    }
    const [z, raw] = await processFiles(e.dataTransfer.files);
    setZips([...zips, ...z]);
    setRawFiles([...rawFiles, ...raw]);
  };

  const onFileChange = async (e: any) => {
    if (isLoading) {
      e.target.value = "";
      notify("Please wait for the previous import to finish.", "error");
      return;
    }
    const [z, raw] = await processFiles(Array.from(e.target.files));
    setZips([...zips, ...z]);
    setRawFiles([...rawFiles, ...raw]);
    e.target.value = "";
  };

  const removeZip = (idx: number, table?: number) => {
    setZips((zips) => {
      const newZips = [...zips];
      if (table !== undefined) {
        newZips[idx].files.splice(table, 1);
      } else {
        newZips.splice(idx, 1);
      }
      return newZips;
    });
  };

  const removeFile = (idx?: number) => {
    setRawFiles((rawFiles) => {
      if (idx === undefined) {
        return [];
      }
      const newRawFiles = [...rawFiles];
      newRawFiles.splice(idx, 1);
      return newRawFiles;
    });
  };

  const doImport = async () => {
    if (isLoading) {
      notify("Please wait for the previous import to finish.", "error");
      return;
    }
    setLoading(true);
    await sendImportRequest({
      setLoadings: setLoadingsRef,
      zips,
      rawFiles,
      keepTitles,
    });
    setLoading(false);
  };

  return (
    <div className="wptb-v2-page">
      <Nav setPage={setPage} page="import" />
      <div className="wptb-v2-page-content">
        <div className="wptb-v2-page-content-top">
          <h3>Import Table</h3>
        </div>
        <div
          className={cn("wptb-v2-page-content-main wptb-v2-import-page", {
            "wptb-v2-import-page-has-files":
              zips.length > 0 || rawFiles.length > 0,
          })}
        >
          <div className="wptb-v2-import-page-main-area">
            <div className="wptb-v2-import-page-file-area">
              <h3>Pick files</h3>
              <div
                className={cn("wptb-v2-import-page-dropzone", {
                  "wptb-v2-import-page-dropzone-dragover": dragOver,
                })}
                onDragOver={(e) => {
                  setDragOver(true);
                  e.preventDefault();
                  e.stopPropagation();
                }}
                onDragLeave={() => setDragOver(false)}
                onDrop={onDrop}
              >
                <p className="wptb-v2-import-page-dropzone-text">
                  Drag and drop your files here
                </p>
                <p className="wptb-v2-import-page-dropzone-types">
                  (csv, xml, zip)
                </p>
                <div className="wptb-v2-divider">or</div>
                <label>
                  Browse files
                  <input
                    type="file"
                    accept=".csv, .xml, .zip"
                    onChange={onFileChange}
                    multiple
                  />
                </label>
              </div>
            </div>
            {(zips.length > 0 || rawFiles.length > 0) && (
              <div className="wptb-v2-import-page-preview">
                <h3>
                  To be imported
                  <button
                    type="button"
                    title="Clear Files"
                    onClick={() => {
                      setZips([]);
                      setRawFiles([]);
                    }}
                  >
                    {TrashIcon}
                  </button>
                </h3>
                {rawFiles.length > 0 && (
                  <div className="wptb-v2-import-page-preview-item">
                    <p>
                      Single tables
                      {!isLoading && (
                        <button type="button" onClick={() => removeFile()}>
                          {TimesIcon}
                        </button>
                      )}
                    </p>
                    <ul>
                      {rawFiles.map((file, i) => (
                        <li key={`${file.name}_${i}`}>
                          {file.name}
                          <div className="wptb-v2-import-page-preview-status">
                            {getStatusIcon(loadings[i])}
                            {!isLoading && (
                              <button
                                type="button"
                                onClick={() => removeFile(i)}
                              >
                                {TimesIcon}
                              </button>
                            )}
                          </div>
                        </li>
                      ))}
                    </ul>
                  </div>
                )}
                {zips.map((zip, z) => (
                  <div
                    key={zip.name}
                    className="wptb-v2-import-page-preview-item"
                  >
                    <p>
                      {zip.name}
                      {!isLoading && (
                        <button type="button" onClick={() => removeZip(z)}>
                          {TimesIcon}
                        </button>
                      )}
                    </p>
                    <ul>
                      {zip.files.map((entry, t) => (
                        <li key={`${entry.object.name}_${t}`}>
                          {entry.object.name}
                          <div className="wptb-v2-import-page-preview-status">
                            {getStatusIcon(loadings[`${z}_${t}`])}
                            {!isLoading && (
                              <button
                                type="button"
                                onClick={() => removeZip(z, t)}
                              >
                                {TimesIcon}
                              </button>
                            )}
                          </div>
                        </li>
                      ))}
                    </ul>
                  </div>
                ))}
              </div>
            )}
          </div>
          <div className="wptb-v2-import-page-form">
            <label>
              <input
                type="checkbox"
                checked={keepTitles}
                onChange={() => setKeepTitles(!keepTitles)}
              />
              <p>Preserve table titles</p>
            </label>
            <button type="button" onClick={doImport} disabled={isLoading}>
              {isLoading ? (
                <UploadingLoopIcon />
              ) : (
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="1em"
                  height="1em"
                  viewBox="0 0 20 20"
                >
                  <path
                    fill="currentColor"
                    d="M9 6c0-1.6.8-3 2-4h-1c-3.9 0-7 .9-7 2c0 1 2.6 1.8 6 2m3.8 4.8c-.9.1-1.9.2-2.8.2c-3.9 0-7-.9-7-2v3c0 1.1 3.1 2 7 2s7-.9 7-2v-2c-.9.7-1.9 1-3 1c-.4 0-.8-.1-1.2-.2M10 15c-3.9 0-7-.9-7-2v3c0 1.1 3.1 2 7 2s7-.9 7-2v-3c0 1.1-3.1 2-7 2m0-5h1c-1-.7-1.7-1.8-1.9-3C5.7 6.9 3 6 3 5v3c0 1.1 3.1 2 7 2m4 0c2.2 0 4-1.8 4-4s-1.8-4-4-4s-4 1.8-4 4s1.8 4 4 4m-1-4V3h2v3h2l-3 3l-3-3z"
                  />
                </svg>
              )}
              Import
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ImportTablePage;
