import { showUpsell } from "@/builder/components/upsell-modal";
import parseTable from "@/legacy/parser";
import notify from "@/public/notify";
import store from "@/store";
import { setTable } from "@/store/table";
import { DownloadingLoopIcon } from "@wptb/ui/icons";
import cn from "classnames";
import Fuse from "fuse.js";
import { useCallback, useEffect, useRef, useState } from "react";
import { v4 as uuidv4 } from "uuid";

interface Pattern {
  name: string;
  title: string;
  height: number;
  width: number;
  is_upsell?: boolean;
}

const PATTERNS: Pattern[] = [];

interface PageProps {
  setPage: (page: string, pageProps?: any) => void;
  search?: string;
}

const Patterns = ({ setPage, search }: PageProps) => {
  const [patterns, setPatterns] = useState(PATTERNS);
  const [isLoading, setIsLoading] = useState(true);
  const [creating, setCreating] = useState<string>();
  const fuse = useRef<Fuse<any> | null>(null);

  useEffect(() => {
    if (PATTERNS.length) {
      setIsLoading(false);
      setPatterns(PATTERNS);
      fuse.current = new Fuse(PATTERNS, {
        includeScore: true,
        threshold: 0.3,
        keys: ["title"],
      });
      return;
    }
    let ctrl: AbortController | undefined = new AbortController();
    fetch(WPTB_CFG.API_BASE + "/v1/patterns", {
      signal: ctrl.signal,
    })
      .then((res) => res.json())
      .then((data) => {
        setIsLoading(false);
        if (PATTERNS.length) {
          return;
        }
        if (data.error) {
          throw new Error(data.error);
        } else {
          setPatterns(data.patterns);
          PATTERNS.push(...data.patterns);
          fuse.current = new Fuse(PATTERNS, {
            includeScore: true,
            threshold: 0.3,
            keys: ["title"],
          });
        }
      })
      .catch(() => {})
      .finally(() => {
        ctrl = undefined;
      });

    return () => ctrl?.abort();
  }, []);
  const handleSearch = useCallback((term: string) => {
    if (term === "") {
      setPatterns(PATTERNS);
    } else {
      const results = fuse.current?.search(term);
      const data = results?.map((result) => result.item);
      setPatterns(data || []);
    }
  }, []);

  useEffect(() => {
    const keyWord = search?.trim();
    if (!keyWord || keyWord.length < 2) {
      setPatterns(PATTERNS);
      return;
    }
    handleSearch(keyWord);
  }, [search, handleSearch]);

  const handleClick = async (pattern: Pattern) => {
    if(pattern.is_upsell) {
      showUpsell('patterns');
      return;
    }
    if (creating) {
      notify("A request is already in progress", "error");
      return;
    }
    setCreating(pattern.name);
    const res = await fetch(
      WPTB_CFG.API_BASE + "/v1/patterns?name=" + pattern.name,
      {
        headers: {
          "Content-Type": "application/json",
        },
      }
    );
    if (!res.ok) {
      notify("Something went wrong", "error");
      setCreating(undefined);
      return;
    }
    const data = await res.json();
    if (data.error) {
      notify(data.error, "error");
      setCreating(undefined);
      return;
    }
    const patternInfo = data.pattern;
    const tableState = parseTable(patternInfo.content)!;
    tableState.title = "Clone of: " + pattern.title;
    const id = uuidv4();
    tableState.id = id;
    store.dispatch(
      setTable({
        id,
        table: tableState,
      })
    );
    setPage("builder", {
      table: id,
    });
  };

  return (
    <div className="wptb-v2-table-templates">
      {isLoading
        ? Array.from({ length: 9 }, (_, i) => (
            <div className="wptb-v2-table-template" key={i}>
              <div className="wptb-v2-table-template-preview">
                <div className="wptb-v2-table-template-preview-loading"></div>
              </div>
              <div className="wptb-v2-table-template-title">
                <div className="wptb-v2-table-template-title-loading"></div>
              </div>
            </div>
          ))
        : patterns.map((p) => (
            <button
              className={cn("wptb-v2-table-template", {
                "wptb-v2-table-template-upsell": p.is_upsell,
              })}
              key={p.name}
              onClick={() => handleClick(p)}
            >
              <div className="wptb-v2-table-template-preview">
                <img
                  src={
                    WPTB_CFG.PLUGIN_URL +
                    "/v2/assets/patterns/images/" +
                    p.name +
                    ".png"
                  }
                  alt={p.name}
                />
                {creating === p.name && (
                  <div className="wptb-v2-table-template-preview-creating">
                    <DownloadingLoopIcon />
                  </div>
                )}
              </div>
              <div className="wptb-v2-table-template-title">{p.title}</div>
            </button>
          ))}
    </div>
  );
};

export default Patterns;
