import { toOldProps } from "@/legacy/saver";
import parseTable from "@/legacy/parser";
import notify, { updateNotification } from "@/public/notify";
import JSZip, { JSZipObject } from "jszip";
import Papa from "papaparse";
import { defaultTableProps } from "@/builder/blocks/default-props";
import { getSingleBorderStr } from "@wptb/utils/style";
import { toDirectives, toResponsiveDirectives } from "@/legacy/saver/utils";

const extractTable = (zip: File): Promise<JSZip> => {
  return new Promise((resolve) => {
    const reader = new FileReader();

    reader.onload = function (e) {
      if (!e.target?.result) {
        return;
      }
      JSZip.loadAsync(e.target.result).then(function (zip) {
        resolve(zip);
      });
    };
    reader.readAsArrayBuffer(zip);
  });
};

export interface ZipEntry {
  name: string;
  files: {
    object: JSZipObject;
    status?: number;
  }[];
}

export const processFiles = async (
  files: File[]
): Promise<[ZipEntry[], File[]]> => {
  const zips: ZipEntry[] = [];
  const rawFiles: File[] = [];
  for (let i = 0; i < files.length; i++) {
    const file = files[i];
    if (file.type === "application/x-zip-compressed") {
      const zip = await extractTable(file);
      const files: ZipEntry["files"] = [];
      zip.forEach(function (_, file) {
        if (file.name.endsWith(".csv") || file.name.endsWith(".xml")) {
          files.push({
            object: file,
          });
        }
      });
      zips.push({ name: file.name, files });
    } else if (file.type === "text/csv") {
      rawFiles.push(file);
    } else if (file.type === "text/xml") {
      rawFiles.push(file);
    } else {
      notify("Unsupported file: " + file.name, "error");
    }
  }
  return [zips, rawFiles];
};

export const flatten = (zips: ZipEntry[]) => {
  const flat: {
    file: number;
    zip: number;
  }[] = [];
  zips.forEach((zip, idx) => {
    zip.files.forEach((_, i) => {
      flat.push({ file: i, zip: idx });
    });
  });
  return flat;
};

const parseWptbTable = (value: string) => {
  const table = parseTable(value);
  if (!table) {
    return;
  }
  return toOldProps(table?.blocksProps, table?.desktop, table?.innerBlocks);
};

const parseCsvTable = (value: string[][]) => {
  if (value.at(-1)?.length === 1) {
    value.pop();
  }

  const cols = value.reduce((acc, row) => Math.max(acc, row.length), 0);

  const tableProps = {
    alignment: defaultTableProps.alignment,
    enableMaxWidth: false,
    cellMinWidth: 120,
    sortHorizontal: false,
    sortVertical: false,
    borderCollapse: "collapse !important",
    tableSpacingX: 3,
    tableSpacingY: 3,
    cols,
    cellsWidthAutoCount: cols,
    tdSumMaxWidth: 120 * cols + 20 * cols,

    // Global Font Style
    fontColor: "#000000",
    linkColor: "#000000",
    fontSize: 15,

    // Border
    tableBorder: getSingleBorderStr(defaultTableProps.tableBorder as any),
    innerBorder: getSingleBorderStr(defaultTableProps.innerBorder as any),
    headerInnerBorder: true,

    // Other
    scrollX: false,
    disableThemeStyles: false,
    extraStyles: btoa(""),
    stickyFirstColumn: false,
    stickyTopRow: false,
    paginationEnable: false,
    paginationTopRowAsHeader: "false",
    rowsPerPage: "10",
    rowsChangeable: false,

    searchEnable: false,
    searchKeepHeader: "true",
    searchPosition: "left",
    role: "table",

    directives: toDirectives(defaultTableProps),
    responsiveDirectives: toResponsiveDirectives(defaultTableProps.responsive),
  };

  const rows: any = [];

  value.forEach((row, yIndex) => {
    const cells: any = [];
    row.forEach((txt, xIndex) => {
      cells.push({
        props: {
          autoWidth: "true",
          autoHeight: "true",
          padding: "10px",
          vAlign: "center",
          xIndex,
          yIndex,
          colspan: 1,
          rowspan: 1,
          hideOnMobile: false,
        },
        blocks: [
          {
            type: "text",
            props: {
              text: txt,
              color: "#000000",
              fontSize: "15px",
            },
          },
        ],
      });
    });
    rows.push({
      props: {},
      cells,
    });
  });

  return JSON.stringify({
    props: tableProps,
    rows,
  });
};

export const parseFile = (file: File): Promise<string | null> => {
  return new Promise((resolve, reject) => {
    if (file.type === "text/csv") {
      Papa.parse(file, {
        complete(results) {
          if (results.errors.length > 0) {
            reject("Failed to parse the file: " + file.name);
            return;
          }
          const data = results.data;
          resolve(parseCsvTable(data as string[][]));
        },
      });
    } else if (file.type === "text/xml") {
      const reader = new FileReader();
      reader.onload = function (e) {
        if (!e.target?.result) {
          reject("No content found!");
          return;
        }
        const data = parseWptbTable(e.target.result as string);
        resolve(JSON.stringify(data));
      };
      reader.readAsText(file);
    } else {
      reject("Unsupported file: " + file.name);
    }
  });
};

export const parseFileFromZip = (file: JSZipObject): Promise<string | null> => {
  return new Promise((resolve, reject) => {
    file.async("text").then((data) => {
      if (file.name.endsWith(".csv")) {
        Papa.parse(data, {
          complete(results) {
            if (results.errors.length > 0) {
              reject("Failed to parse the file: " + file.name);
              return;
            }
            const data = results.data;
            resolve(parseCsvTable(data as string[][]));
          },
        });
      } else if (file.name.endsWith(".xml")) {
        const data2 = parseWptbTable(data);
        resolve(JSON.stringify(data2));
      } else {
        reject("Unsupported file: " + file.name);
        resolve(null);
      }
    });
  });
};

interface ImportProps {
  setLoadings: {
    current: (arg0: any) => void;
  };
  zips: any;
  rawFiles: any;
  keepTitles: boolean;
}

export const sendImportRequest = async ({
  setLoadings,
  zips,
  rawFiles,
  keepTitles,
}: ImportProps) => {
  setLoadings.current({});
  const zipped = flatten(zips);
  const total = zipped.length + rawFiles.length;
  const progress = notify(`[0/${total}] Importing tables...`, "progress");
  let importDone = 1;

  // Import 5 at a time to prevent overloading the network requests
  let start = 0;
  while (start < total) {
    const requests: Promise<any>[] = [];
    for (let i = 0; i < 5; i++) {
      let status_key: any = "";
      try {
        let idx = start + i;
        let oldProps: string | null = null;
        let title: string | undefined;
        let file: any;
        if (idx < rawFiles.length) {
          file = rawFiles[idx];
          status_key = idx;
          oldProps = await parseFile(file);
          title = file.name.replace(/\.([a-z]+)$/, "");
        } else if (idx < total) {
          idx = idx - rawFiles.length;
          const entry = zipped[idx];
          const zip = zips[entry.zip];
          file = zip.files[entry.file];
          status_key = entry.zip + "_" + entry.file;
          oldProps = await parseFileFromZip(file.object);
          title = file.object.name.replace(/\.([a-z]+)$/, "");
        }
        if (!oldProps) {
          importDone++;
          continue;
        }
        setLoadings.current((loadings: any) => ({
          ...loadings,
          [status_key]: 1,
        }));
        requests.push(
          new Promise((resolve) => {
            fetch(WPTB_CFG.ADMIN_URL + "?action=save_table", {
              method: "POST",
              headers: {
                "Content-type": "application/json",
              },
              body: JSON.stringify({
                title: keepTitles ? title : undefined,
                content: oldProps,
                security_code: WPTB_CFG.SECURITY_CODE,
              }),
            })
              .then((res) => {
                if (res.ok) {
                  setLoadings.current((loadings: any) => ({
                    ...loadings,
                    [status_key]: 2,
                  }));
                } else {
                  throw new Error(res.statusText);
                }
              })
              .catch((e) => {
                setLoadings.current((loadings: any) => ({
                  ...loadings,
                  [status_key]: e.message || "Something went wrong...",
                }));
              })
              .finally(() => {
                importDone++;
                resolve(true);
              });
          })
        );
      } catch (e: any) {
        importDone++;
        setLoadings.current((loadings: any) => ({
          ...loadings,
          [status_key]:
            typeof e === "string" ? e : e.message || "Something went wrong...",
        }));
      } finally {
        updateNotification(progress, {
          progress: (importDone / total) * 100,
          message: `[${importDone}/${total}] Importing tables...`,
        });
      }
    }
    await Promise.all(requests);
    start += 5;
  }

  updateNotification(progress, {
    type: "success",
    message: `[${total}] Tables imported successfully!`,
    spawned_at: Date.now(),
    duration: 5000,
  });
};
