import { useEffect, useRef, useState } from "react";
import Nav from "../Nav";
import Pagination from "./Pagination";
import { useTablePage } from "@wptb/utils/react";
import TheTable from "./TheTable";
import cn from "classnames";

interface PageProps {
  setPage: (page: string, pageProps?: any) => void;
  page_num?: number | string;
  search?: string;
  status?: string;
  sort_by?: string;
  sort_order?: string;
}
const TableListPage = ({
  setPage,
  page_num,
  search,
  status,
  sort_by,
  sort_order,
}: PageProps) => {
  const abortCtrl = useRef<AbortController>();
  const tOutRef = useRef<NodeJS.Timeout>();
  const [searchTxt, setSearch] = useState(search);

  const [apiData, setPageTables] = useTablePage({
    page: page_num ? parseInt(page_num as any) : 1,
    search,
    status,
    sort: {
      by: sort_by || "id",
      order: (sort_order as any) || "desc",
    },
    abortCtrl: abortCtrl.current,
  });

  useEffect(() => {
    const newSearch = searchTxt?.trim();
    if (!search) {
      if (!newSearch || newSearch.length < 2) {
        return;
      }
    } else if (newSearch && newSearch.length > 0 && newSearch.length < 2) {
      return;
    }
    if (tOutRef.current) {
      clearTimeout(tOutRef.current);
    }
    if (abortCtrl.current) {
      abortCtrl.current.abort();
    }
    abortCtrl.current = new AbortController();
    tOutRef.current = setTimeout(() => {
      setPage("tables", {
        search: searchTxt,
        status,
      });
      abortCtrl.current = undefined;
      tOutRef.current = undefined;
    }, 500);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [searchTxt]);

  const setSort = (by: string) => {
    const sortBy = sort_by || "id";
    setPage("tables", {
      page_num: 1,
      search,
      status,
      sort_by: by,
      sort_order:
        by === sortBy ? (sort_order === "asc" ? "desc" : "asc") : "asc",
    });
  };

  const isTrash = status === "trash";

  return (
    <div className="wptb-v2-page">
      <Nav setPage={setPage} page="tables" />
      <div className="wptb-v2-page-content">
        <div className="wptb-v2-page-content-top">
          <h2>Table List</h2>
          <div className="wptb-v2-table-status-btns">
            <button
              type="button"
              className={cn({
                "wptb-v2-active-section": !isTrash,
              })}
              onClick={() => {
                setPage("tables", {});
              }}
            >
              ALL
            </button>
            <button
              type="button"
              className={cn({
                "wptb-v2-active-section": isTrash,
              })}
              onClick={() => {
                setPage("tables", {
                  status: "trash",
                });
              }}
            >
              TRASH
            </button>
          </div>
        </div>

        <div className="wptb-v2-table-list-area">
          <p className="wptb-v2-pagination-info">
            Showing <b>{apiData.page_start}</b> to <b>{apiData.page_end}</b> of{" "}
            <b>{apiData.total_count}</b> entries
          </p>

          <TheTable
            apiData={apiData}
            setPage={setPage}
            isTrash={isTrash}
            setTables={setPageTables}
            setSort={setSort}
            sortBy={sort_by}
            sortOrder={sort_order}
            search={searchTxt}
            setSearch={setSearch}
          />
          <Pagination
            apiData={apiData}
            setPage={(p) =>
              setPage("tables", { page_num: p, search, sort_by, sort_order })
            }
          />
        </div>
      </div>
    </div>
  );
};

export default TableListPage;
