import notify from "@/public/notify";
import { LongTextControl } from "@wptb/ui/components";
import { LoadingSpinnerIcon } from "@wptb/ui/icons";
import { useState } from "react";

const TableStylesSetting = () => {
  const [styles, setStyles] = useState(WPTB_CFG.SETTINGS?.table_style || "");
  const [isLoading, setIsLoading] = useState(false);
  const save = async () => {
    if (isLoading) return;
    setIsLoading(true);
    try {
      const fd = new FormData();
      fd.append("nonce", WPTB_CFG.SETTINGS.nonce.styles);
      fd.append("action", "wptb-general-styles");
      fd.append("styles", styles);

      const res = await fetch(WPTB_CFG.ADMIN_URL, {
        method: "POST",
        body: fd,
      });
      const data = await res.json();

      if (!res.ok) {
        notify(data.message || "Something went wrong", "error");
        return;
      }

      if (data.error) {
        notify(data.error, "error");
        return;
      }

      notify(data.message || "Settings saved successfully", "success");
      WPTB_CFG.SETTINGS.table_style = styles as any;
    } catch (err) {
      console.log(err);
    } finally {
      setIsLoading(false);
    }
  };

  const revert = () => {
    setStyles(WPTB_CFG.SETTINGS.table_style || "");
  };
  return (
    <div>
      <div className="wptb-v2-settings-area wptb-v2-table-settings">
        <p>These styles will be applied to all tables.</p>
        <LongTextControl
          label="Custom CSS"
          rows={10}
          value={styles}
          onChange={setStyles}
        />
      </div>
      <div className="wptb-v2-setting-btns">
        <button
          type="button"
          className="wptb-btn-revert"
          onClick={revert}
          disabled={isLoading}
        >
          Revert
        </button>
        <button
          type="button"
          className="wptb-btn-save"
          onClick={save}
          disabled={isLoading}
        >
          {isLoading && LoadingSpinnerIcon} Save
        </button>
      </div>
    </div>
  );
};

export default TableStylesSetting;
