import { RestoreIcon, TrashIcon, WarningIcon } from "@wptb/ui/icons";

export const ACTIONS: Record<
  string,
  {
    icon: any;
    title: string;
    text: (data?: any, count?: number) => string;
    className?: string;
    url: string;
  }
> = {
  duplicate: {
    icon: WarningIcon,
    title: "Sure to duplicate?",
    text: (m) =>
      `Table: <b>${m.targetTitle}</b> is set to be duplicated.<br/> Are you sure?`,
    className: "wptb-modal-warning",
    url: "duplicate",
  },
  trash: {
    icon: TrashIcon,
    title: "Sure to trash?",
    text: (m) =>
      `Table: <b>${m.targetTitle}</b> is set to be trashed. <br />Are you sure?`,
    className: "wptb-modal-danger",
    url: "trash",
  },
  restore: {
    icon: RestoreIcon,
    title: "Sure to restore?",
    text: (m) =>
      `Table: <b>${m.targetTitle}</b> is set to be trashed. <br />Are you sure?`,
    className: "wptb-modal-warning",
    url: "restore",
  },
  delete: {
    icon: TrashIcon,
    title: "Sure to delete?",
    text: (modal) =>
      `Table: <b>${modal.targetTitle}</b> is set to be deleted. Are you sure?<br/> It will be deleted permanently.`,
    className: "wptb-modal-danger",
    url: "delete",
  },
  trash_bulk: {
    icon: TrashIcon,
    title: "Sure to trash?",
    text: (_, c) =>
      `<b>${c}</b> table(s) are set to be trashed. <br />Are you sure?`,
    className: "wptb-modal-danger",
    url: "trash_bulk",
  },
  restore_bulk: {
    icon: RestoreIcon,
    title: "Sure to restore?",
    text: (_, c) =>
      `<b>${c}</b> table(s) are set to be restored. <br />Are you sure?`,
    className: "wptb-modal-warning",
    url: "restore_bulk",
  },
};

interface TableAction {
  action: string;
  id?: number;
  ids?: number[];
}

export const applyTableAction = async ({
  action,
  id,
  ids,
}: TableAction): Promise<TableAction & { res: any }> => {
  const url = ACTIONS[action].url;
  if (!url) {
    return { action, id, ids, res: {} };
  }
  try {
    const res = await fetch(WPTB_CFG.API_BASE + "/v1/" + url, {
      method: "POST",
      headers: {
        "Content-type": "application/json",
      },
      body: JSON.stringify({
        id,
        ids,
        security_code: WPTB_CFG.SECURITY_CODE,
      }),
    });
    const data = await res.json();

    if (!res.ok) {
      throw new Error(data.message || `HTTP error! Status: ${res.status}`);
    }

    return { action, id, ids, res: data };
  } catch (e: any) {
    return { action, id, ids, res: { error: e.message } };
  }
};
