import { createNewTable } from "@/public/utils";
import RangeControl from "@wptb/ui/components/range-control";
import { useState } from "react";

interface Props {
  setPage: (page: string, pageProps?: any) => void;
}

const NewTable = ({ setPage }: Props) => {
  const [rows, setRows] = useState<number>(4);
  const [cols, setCols] = useState<number>(4);

  const create = () => {
    const table = createNewTable(rows, cols);
    setPage("builder", {
      table,
    });
  };
  return (
    <div className="wptb-v2-create-table">
      <table>
        <tbody>
          {Array(rows)
            .fill(0)
            .map((_, i) => (
              <tr key={i}>
                {Array(cols)
                  .fill(0)
                  .map((_, j) => (
                    <td key={j} />
                  ))}
              </tr>
            ))}
        </tbody>
      </table>
      <div className="wptb-v2-create-table-form">
        <div className="wptb-v2-create-table-form-input">
          <RangeControl
            label="Number of rows"
            min={2}
            max={20}
            value={rows}
            onChange={setRows}
            allowOutOfRange
          />
        </div>
        <div className="wptb-v2-create-table-form-input">
          <RangeControl
            label="Number of columns"
            min={2}
            max={20}
            value={cols}
            onChange={setCols}
            allowOutOfRange
          />
        </div>
        <button type="button" className="wptb-v2-create-btn" onClick={create}>
          Create New
        </button>
      </div>
    </div>
  );
};

export default NewTable;
