import React from "react";
import { createRoot } from "react-dom/client";
import { Provider } from "react-redux";
import WptbPages from "./pages";
import { Notifications } from "./public/notify";
import store from "./store";
import { UpsellView } from "./builder/components/upsell-modal";

document.addEventListener("DOMContentLoaded", () => {
  const el = document.querySelector("#wptb-app-root");

  if (!el || typeof WPTB === "undefined") {
    return;
  }
  createRoot(el).render(
    <React.StrictMode>
      <Provider store={store}>
        <WptbPages />
        <Notifications />
        <UpsellView />
      </Provider>
    </React.StrictMode>
  );
});
