import { __ } from "@wptb/utils/i18n";
import Nav from "../Nav";
import GeneralSettings from "./GeneralSettings";
import { createElement, useState } from "react";
import TableStylesSetting from "./TableStylesSetting";
import LazyLoadSettings from "./LazyLoadSettings";
import VersionSettings from "./VersionSettings";
import notify from "@/public/notify";
import { Modal } from "@wptb/ui/components";
import { LoadingSpinnerIcon, WarningIcon } from "@wptb/ui/icons";

interface PageProps {
  setPage: (page: string, pageProps?: any) => void;
}

const TABS: Record<string, any> = {
  general: GeneralSettings,
  tableStyles: TableStylesSetting,
  lazyLoad: LazyLoadSettings,
  version: VersionSettings,
};

const SettingsPage = ({ setPage }: PageProps) => {
  const [activeTab, setActiveTab] = useState("general");

  const [isLoading, setIsLoading] = useState(false);
  const [isModalOpen, setModal] = useState(false);

  const makeSwitch = async () => {
    setModal(false);
    if (isLoading) return;
    setIsLoading(true);
    try {
      const fd = new FormData();
      fd.append("nonce", WPTB_CFG.SETTINGS.nonce.general);
      fd.append("action", "wptb_toggle_legacy_builder");
      const res = await fetch(WPTB_CFG.ADMIN_URL, {
        method: "POST",
        body: fd,
      });
      const data = await res.json();
      if (!res.ok) {
        notify(data.message || "Something went wrong", "error");
        return;
      }
      if (data.error) {
        notify(data.error, "error");
        return;
      }
      notify(data.message || "Settings saved successfully", "success");
      window.location.replace(
        window.location.href.replace(
          "wptb-settings",
          "wp-table-builder-settings"
        )
      );
    } catch (err) {
      console.log(err);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="wptb-v2-page wptb-v2-settings-page">
      <Nav setPage={setPage} page="settings" />
      <div className="wptb-v2-page-content">
        <div className="wptb-v2-page-content-top">
          <h2>{__("WP Table Builder")}</h2>
          <button
            type="button"
            className="wptb-v2-switch-builder"
            disabled={isLoading}
            onClick={() => setModal(true)}
          >
            {isLoading && LoadingSpinnerIcon} Switch to Legacy Builder
          </button>
        </div>
        <div className="wptb-v2-page-content-main">
          <div className="wptb-v2-settings-tabs">
            <button
              className={activeTab === "general" ? "wptb-v2-active" : ""}
              onClick={() => setActiveTab("general")}
            >
              General
            </button>
            <button
              className={activeTab === "tableStyles" ? "wptb-v2-active" : ""}
              onClick={() => setActiveTab("tableStyles")}
            >
              Table Styles
            </button>
            <button
              className={activeTab === "lazyLoad" ? "wptb-v2-active" : ""}
              onClick={() => setActiveTab("lazyLoad")}
            >
              Lazy Load
            </button>
            <button
              className={activeTab === "version" ? "wptb-v2-active" : ""}
              onClick={() => setActiveTab("version")}
            >
              Version Control
            </button>
          </div>
          <div>{TABS[activeTab] && createElement(TABS[activeTab])}</div>
        </div>
      </div>
      {isModalOpen && (
        <Modal
          onClose={() => setModal(false)}
          title="Are you sure to switch?"
          icon={WarningIcon}
        >
          <p>
            Are you sure you want to miss out on the new features and next
            generation experience? You can always switch back to the legacy
            builder.
          </p>
          <div className="wptb-v2-modal-actions">
            <button type="button" onClick={makeSwitch}>
              Yes
            </button>
            <button type="button" onClick={() => setModal(false)}>
              No
            </button>
          </div>
        </Modal>
      )}
    </div>
  );
};

export default SettingsPage;
