import { EnhancementGroup } from "@/builder/components/enhanced-control";
import UpsellControl from "@/builder/components/upsell-control";
import notify from "@/public/notify";
import {
  ColorControl,
  IconPicker,
  PanelBody,
  RangeControl,
  SelectControl,
  SizeControl,
  SwitchControl,
} from "@wptb/ui/components";
import { LoadingSpinnerIcon } from "@wptb/ui/icons";
import { useState } from "react";

const LazyLoadSettings = () => {
  const [isLoading, setIsLoading] = useState(false);
  const [isPreviewed, setIsPreviewed] = useState(false);

  const [lazyLoadSettings, setLazyLoadSettings] = useState(
    WPTB_CFG.SETTINGS?.lazy_load || {
      enabled: false,
    }
  );

  const [proSettings, setProSettings] = useState(
    WPTB_CFG.SETTINGS.lazy_load_pro!
  );

  const save = async () => {
    if (isLoading) return;
    setIsLoading(true);
    try {
      const fd = new FormData();
      fd.append("nonce", WPTB_CFG.SETTINGS.nonce.lazy_load);
      fd.append("action", "wptb_lazy_load");
      const settings: typeof proSettings &
        typeof lazyLoadSettings & {
          iconAnimationOptions: any;
          imageLoadAnimationOptions: any;
        } = {
        ...lazyLoadSettings,
        ...proSettings,
        visibilityPercentage: parseInt(
          proSettings.visibilityPercentage
        ).toString(),
        iconSize: parseInt(proSettings.iconSize).toString(),
        imageLoadAnimationSpeed: parseInt(
          proSettings.imageLoadAnimationSpeed
        ).toString(),
        imageLoadAnimationPerspective: parseInt(
          proSettings.imageLoadAnimationPerspective
        ).toString(),
        iconAnimationOptions: {
          none: "none",
          heartBeat: "heartbeat",
          rotate: "rotate",
          flip: "flip",
          jump: "jump",
        },
        imageLoadAnimationOptions: {
          none: "none",
          slideIn: "slide in",
          growSling: "grow sling",
          flash: "flash",
          flip: "flip",
        },
      };
      fd.append("settings", JSON.stringify(settings));

      const res = await fetch(WPTB_CFG.ADMIN_URL, {
        method: "POST",
        body: fd,
      });
      const data = await res.json();

      if (!res.ok) {
        notify(data.message || "Something went wrong", "error");
        return;
      }

      if (data.error) {
        notify(data.error, "error");
        return;
      }

      notify(data.message || "Settings saved successfully", "success");
      WPTB_CFG.SETTINGS.lazy_load = lazyLoadSettings;
      WPTB_CFG.SETTINGS.lazy_load_pro = proSettings;
    } catch (err) {
      console.log(err);
    } finally {
      setIsLoading(false);
    }
  };

  const revert = () => {
    setLazyLoadSettings(WPTB_CFG.SETTINGS.lazy_load!);
    setProSettings(WPTB_CFG.SETTINGS.lazy_load_pro!);
  };

  return (
    <div>
      <div className="wptb-v2-settings-area wptb-v2-lazy-load-settings">
        <div>
          <PanelBody
            title="Enable Lazy Load"
            initialOpen
            className="wptb-v2-lazy-load-group"
          >
            <SwitchControl
              label="Enable Lazy Loading Images"
              checked={!!lazyLoadSettings.enabled}
              onChange={(checked) => setLazyLoadSettings({ enabled: checked })}
            />
          </PanelBody>
          <div className="wptb-v2-lazy-load-preview">
            <h4>Preview</h4>
            <div className="wptb-v2-lazy-load-preview-image">
              <img
                src={WPTB_CFG.PLUGIN_URL + "/v2/assets/images/wptb-logo.png"}
                className={`${proSettings.imageLoadAnimation} to-${proSettings.imageLoadAnimationDirection}`}
                style={
                  {
                    "--transition-speed":
                      1 / Number(proSettings.imageLoadAnimationSpeed) + "s",
                  } as any
                }
                data-visible={isPreviewed}
              />
              {proSettings?.imageLoadAnimation === "flash" && (
                <div
                  style={{
                    background: proSettings.flashColor,
                  }}
                />
              )}
            </div>
            <div className="wptb-v2-lazy-load-preview-btns">
              <button
                type="button"
                className="wptb-v2-lazy-load-preview-reset"
                disabled={!isPreviewed}
                onClick={() => setIsPreviewed(false)}
              >
                Reset
              </button>
              <button
                type="button"
                className="wptb-v2-lazy-load-preview-reload"
                disabled={isPreviewed}
                onClick={() => setIsPreviewed(true)}
              >
                Load Image
              </button>
            </div>
            <div className="wptb-v2-lazy-load-notice">
              <h5>important:</h5>
              <p>
                After enable, if you are getting any conflicts with your third
                party lazy load plugin, please whitelist table image elements
                with wptb-image-element-target class name from plugin settings.
              </p>
            </div>
          </div>
        </div>
        <div className="wptb-v2-lazy-load-pro">
          <EnhancementGroup
            name="global.lazy_load"
            props={proSettings}
            setProps={setProSettings}
          />
          <UpsellControl name="global.lazy_load">
            <PanelBody
              title="General Options"
              initialOpen
              className="wptb-v2-lazy-load-group"
            >
              <SizeControl
                label="Visibility Percentage"
                value={proSettings?.visibilityPercentage}
                allowedUnits={{ "%": {} }}
              />
              <ColorControl
                label="Background Color"
                value={proSettings?.backgroundColor}
              />
            </PanelBody>
            <PanelBody
              title="Icon Options"
              initialOpen
              className="wptb-v2-lazy-load-group"
            >
              <IconPicker label="Icon" value={proSettings?.iconName?.name} />
              <ColorControl label="Icon Color" value={proSettings?.iconColor} />
              <SizeControl label="Icon Size" value={proSettings?.iconSize} />
              <SelectControl
                label="Icon Animation"
                value={proSettings?.iconAnimation}
                options={[
                  {
                    value: "none",
                    label: "None",
                  },
                  {
                    value: "heartbeat",
                    label: "Heartbeat",
                  },
                  {
                    value: "rotate",
                    label: "Rotate",
                  },
                  {
                    value: "flip",
                    label: "Flip",
                  },
                  {
                    value: "jump",
                    label: "Jump",
                  },
                ]}
              />
            </PanelBody>
            <PanelBody
              title="Image Load Options"
              initialOpen
              className="wptb-v2-lazy-load-group"
            >
              <SelectControl
                label="Animation"
                value={proSettings?.imageLoadAnimation}
                options={[
                  {
                    value: "none",
                    label: "None",
                  },
                  {
                    value: "slide-in",
                    label: "Slide In",
                  },
                  {
                    value: "grow-sling",
                    label: "Grow Sling",
                  },
                  {
                    value: "flash",
                    label: "Flash",
                  },
                  {
                    value: "flip",
                    label: "Flip",
                  },
                ]}
              />
              {proSettings?.imageLoadAnimation === "slide-in" && (
                <SelectControl
                  label="Direction"
                  value={proSettings?.imageLoadAnimationDirection}
                  options={[
                    {
                      value: "left",
                      label: "To Left",
                    },
                    {
                      value: "right",
                      label: "To Right",
                    },
                    {
                      value: "top",
                      label: "To Top",
                    },
                    {
                      value: "bottom",
                      label: "To Bottom",
                    },
                  ]}
                />
              )}
              {proSettings?.imageLoadAnimation === "flash" && (
                <ColorControl
                  label="Flash Color"
                  value={proSettings?.flashColor}
                />
              )}
              {proSettings?.imageLoadAnimation === "flip" && (
                <>
                  <SelectControl
                    label="Direction"
                    value={proSettings?.imageLoadAnimationDirection}
                    options={[
                      {
                        value: "left",
                        label: "To Left",
                      },
                      {
                        value: "right",
                        label: "To Right",
                      },
                    ]}
                  />
                  <SizeControl
                    label="Perspective"
                    value={proSettings?.imageLoadAnimationPerspective}
                  />
                </>
              )}
              <RangeControl
                label="Speed"
                value={
                  (Number(proSettings?.imageLoadAnimationSpeed) as any) || 5
                }
                max={10}
              />
            </PanelBody>
          </UpsellControl>
        </div>
      </div>
      <div className="wptb-v2-setting-btns">
        <button
          type="button"
          className="wptb-btn-revert"
          onClick={revert}
          disabled={isLoading}
        >
          Revert
        </button>
        <button
          type="button"
          className="wptb-btn-save"
          onClick={save}
          disabled={isLoading}
        >
          {isLoading && LoadingSpinnerIcon} Save
        </button>
      </div>
    </div>
  );
};

export default LazyLoadSettings;
