import notify from "@/public/notify";
import { WPTB_CFG as WPTB_CFG_TYPE } from "@wptb/dev/types/global";
import {
  CheckBoxControl,
  SelectControl,
  SwitchControl,
} from "@wptb/ui/components";
import { LoadingSpinnerIcon } from "@wptb/ui/icons";
import { useState } from "react";

const GeneralSettings = () => {
  const [isLoading, setIsLoading] = useState(false);
  const [settings, setSettings] = useState<
    Partial<NonNullable<WPTB_CFG_TYPE["SETTINGS"]["general"]>>
  >(WPTB_CFG.SETTINGS.general || {});
  const toggleRole = (role: string) => {
    if (settings.allowed_roles?.includes(role)) {
      const alloedRoles = settings.allowed_roles.filter((r) => r !== role);
      setSettings({ ...settings, allowed_roles: alloedRoles });
    } else {
      const alloedRoles = [...(settings.allowed_roles || []), role];
      setSettings({ ...settings, allowed_roles: alloedRoles });
    }
  };

  const save = async () => {
    if (isLoading) return;
    setIsLoading(true);
    try {
      const fd = new FormData();
      fd.append("nonce", WPTB_CFG.SETTINGS.nonce.general);
      fd.append("action", "wp_table_builder_settings");
      fd.append("options", JSON.stringify(settings));

      const res = await fetch(WPTB_CFG.ADMIN_URL, {
        method: "POST",
        body: fd,
      });
      const data = await res.json();

      if (!res.ok) {
        notify(data.message || "Something went wrong", "error");
        return;
      }

      if (data.error) {
        notify(data.error, "error");
        return;
      }

      notify(data.message || "Settings saved successfully", "success");
      WPTB_CFG.SETTINGS.general = settings as any;
    } catch (err) {
      console.log(err);
    } finally {
      setIsLoading(false);
    }
  };

  const revert = () => {
    setSettings(WPTB_CFG.SETTINGS.general || {});
  };

  return (
    <div>
      <div className="wptb-v2-general-settings wptb-v2-settings-area">
        <div className="wptb-v2-setting-group">
          <h3>Allowed User Roles</h3>
          <div className="wptb-v2-setting-group-items">
            <CheckBoxControl
              label="Administrator"
              checked={settings.allowed_roles?.includes("administrator")}
              onChange={() => toggleRole("administrator")}
            />
            <CheckBoxControl
              label="Editor"
              checked={settings.allowed_roles?.includes("editor")}
              onChange={() => toggleRole("editor")}
            />
            <CheckBoxControl
              label="Author"
              checked={settings.allowed_roles?.includes("author")}
              onChange={() => toggleRole("author")}
            />
            <CheckBoxControl
              label="Contributor"
              checked={settings.allowed_roles?.includes("contributor")}
              onChange={() => toggleRole("contributor")}
            />
            <CheckBoxControl
              label="Subscriber"
              checked={settings.allowed_roles?.includes("subscriber")}
              onChange={() => toggleRole("subscriber")}
            />
            <CheckBoxControl
              label="Translator"
              checked={settings.allowed_roles?.includes("translator")}
              onChange={() => toggleRole("translator")}
            />
          </div>
        </div>
        <div className="wptb-v2-setting-group">
          <h3>Table Settings</h3>
          <div className="wptb-v2-setting-group-items">
            <CheckBoxControl
              label="Show edit link in frontend"
              checked={!!settings.allow_edit_link_frontend}
              onChange={() =>
                setSettings({
                  ...settings,
                  allow_edit_link_frontend: !settings.allow_edit_link_frontend,
                })
              }
            />
            <CheckBoxControl
              label="Give credits to WP Table Builder"
              checked={!!settings.give_credits_to_wp_table_builder}
              onChange={() =>
                setSettings({
                  ...settings,
                  give_credits_to_wp_table_builder:
                    !settings.give_credits_to_wp_table_builder,
                })
              }
            />
            <CheckBoxControl
              label="Restrict users access to own tables"
              checked={!!settings.restrict_users_to_their_tables}
              onChange={() =>
                setSettings({
                  ...settings,
                  restrict_users_to_their_tables:
                    !settings.restrict_users_to_their_tables,
                })
              }
            />
            <CheckBoxControl
              label="Disable Emoji to Image conversion"
              checked={!!settings.emoji_image_conversion_disabled_status}
              onChange={() =>
                setSettings({
                  ...settings,
                  emoji_image_conversion_disabled_status:
                    !settings.emoji_image_conversion_disabled_status,
                })
              }
            />
            <CheckBoxControl
              label="Disable theme styles"
              checked={!!settings.disable_theme_styles_for_all}
              onChange={() =>
                setSettings({
                  ...settings,
                  disable_theme_styles_for_all:
                    !settings.disable_theme_styles_for_all,
                })
              }
            />
          </div>
        </div>
        <div className="wptb-v2-setting-group">
          <h3>Plugin Options</h3>
          <div className="wptb-v2-setting-group-items">
            <SwitchControl
              rtl
              label="Take over the entire screen in builder"
              checked={!!settings.take_over_entire_screen}
              onChange={(value) =>
                setSettings({ ...settings, take_over_entire_screen: value })
              }
            />
            <SelectControl
              label="Sidebar Location"
              value={settings.panel_location || "left"}
              onChange={(value: any) =>
                setSettings({ ...settings, panel_location: value })
              }
              options={[
                { value: "left", label: "Left" },
                { value: "right", label: "Right" },
              ]}
            />
            <CheckBoxControl
              label="Enable Version Sync"
              checked={!!settings.enable_version_sync}
              onChange={() =>
                setSettings({
                  ...settings,
                  enable_version_sync: !settings.enable_version_sync,
                })
              }
            />
          </div>
        </div>
      </div>
      <div className="wptb-v2-setting-btns">
        <button
          type="button"
          className="wptb-btn-revert"
          onClick={revert}
          disabled={isLoading}
        >
          Revert
        </button>
        <button
          type="button"
          className="wptb-btn-save"
          onClick={save}
          disabled={isLoading}
        >
          {isLoading && LoadingSpinnerIcon} Save
        </button>
      </div>
    </div>
  );
};

export default GeneralSettings;
