import { useEffect, useState } from "react";
import TableListPage from "./table-list";
import CreateTablePage from "./create";
import ImportTablePage from "./import";
import ExportTablePage from "./export";
import BuilderPage from "./builder";
import SettingsPage from "./settings";

const PAGES: Record<string, any> = {
  tables: TableListPage,
  builder: BuilderPage,
  create: CreateTablePage,
  import: ImportTablePage,
  export: ExportTablePage,
  settings: SettingsPage,
};

const getPageParams = () => {
  const params = new URLSearchParams(window.location.search);
  const paramsObj: Record<string, string> = {};
  for (const [key, value] of params) {
    paramsObj[key] = value;
  }
  paramsObj.page =
    paramsObj.page?.replace("wptb", "")?.substring(1) || "tables";
  return paramsObj;
};

function useManageHistory() {
  const [searchParams, setSearchParams] = useState<any>({});

  const updateSearchParam = (
    page: string,
    newParams: Record<string, string>
  ) => {
    newParams = newParams || {};

    const urlPage = page === "tables" ? "wptb" : `wptb-${page}`;
    const params: any = {
      page: urlPage,
    };
    for (const i in newParams) {
      const p = newParams[i];
      if (p) {
        params[i] = p;
      }
    }
    const currentParams = new URLSearchParams(params).toString();

    window.history.pushState(null, "", "?" + currentParams);
    newParams.page = page;
    setSearchParams(newParams);
  };

  useEffect(() => {
    const handlePopState = () => {
      const paramsObj = getPageParams();
      setSearchParams(paramsObj);
    };
    setSearchParams(getPageParams());
    window.addEventListener("popstate", handlePopState);
    return () => {
      window.removeEventListener("popstate", handlePopState);
    };
  }, []);

  return [searchParams, updateSearchParam];
}

const WptbPages = () => {
  const [pageProps, setPageProps] = useManageHistory();
  const Page = PAGES[pageProps.page];
  if (!Page) {
    return;
  }
  return <Page {...pageProps} setPage={setPageProps} />;
};

export default WptbPages;
